import React from "react";
import { FaTwitter, FaGithub, FaMedium } from "react-icons/fa";
import { StaticQuery, graphql } from "gatsby";
import "./style.scss";
import Emoji from "./emoji";

const Footer = () => (
    <StaticQuery
        query={graphql`
            query SocialQuery {
                site {
                    siteMetadata {
                        gatsby
                        bulma
                        github
                    }
                }
            }
        `}
        render={data => (
            <footer className="footer center has-background-dark">
                <div className="content has-text-centered">
                    <p className="is-size-6">
                        Made with <Emoji emoji="❤️" /> by Zandraa Perjési
                    </p>
                    <p className="is-size-6">
                        Powered by{" "}
                        <a href={data.site.siteMetadata.gatsby}>Gatsby</a> +{" "}
                        <a href={data.site.siteMetadata.bulma}>Bulma</a>
                    </p>
                    <p className="is-size-6">
                        Hosted on{" "}
                        <a href={data.site.siteMetadata.github}>Github</a>
                    </p>
                </div>
            </footer>
        )}
    />
);

export default Footer;
