import React from "react";
import {
    FaComment,
    FaHtml5,
    FaReact,
    FaMobileAlt,
    FaRocket,
    FaOsi
} from "react-icons/fa";

import "./style.scss";
import avatar from "../images/avatar.png";
import Emoji from "./emoji";

const Midsection = () => (
    <div>
        <a name="about" />
        <section className="section is-large">
            <div className="container">
                <div className="columns is-multiline">
                    <div className="column is-one-fifths is-hidden-mobile">
                        <figure class="image is-1by1">
                            <img
                                class="is-rounded"
                                src={avatar}
                                alt="my avatar"
                            />
                        </figure>
                    </div>
                    <div className="column is-four-fifths">
                        <article className="media">
                            <figure className="media-left">
                                <span className="icon is-medium">
                                    <FaComment size="fa-2x" color="#d22780" />
                                </span>
                            </figure>
                            <div className="media-content">
                                <div className="content">
                                    <h1 className="title is-size-4">
                                        About me
                                    </h1>
                                    <p className="subtitle is-size-5">
                                        <ul>
                                            <li>My name is Zandraa Perjési</li>
                                            <li>I live in Budapest</li>
                                            <li>
                                                I've been a developer{" "}
                                                <Emoji emoji="👨🏻‍💻" /> since 2017
                                            </li>
                                            <li>
                                                Nowadays I mainly work with
                                                Python <Emoji emoji="🐍" /> and
                                                Java <Emoji emoji="☕" /> on AWS{" "}
                                                <Emoji emoji="☁️" />
                                            </li>
                                            <li>
                                                I am a backend developer but I
                                                like to play around with
                                                frontend stuff in my free time
                                            </li>
                                            <li>
                                                I have a cat{" "}
                                                <Emoji emoji="🐱" /> called
                                                Morzsa
                                            </li>
                                        </ul>
                                    </p>
                                </div>
                            </div>
                        </article>
                    </div>
                </div>
            </div>
        </section>
    </div>
);

export default Midsection;
