import React from "react";
import { FaLinkedinIn, FaAngleDoubleDown, FaGithubAlt } from "react-icons/fa";

import "./style.scss";

import Navbar from "./navbar";

const Header = ({ siteTitle }) => (
    <section className="hero gradientBg is-fullheight">
        <Navbar />
        <div className="hero-body show-scroll">
            <div className="container center">
                <article className="media">
                    <div className="media-content">
                        <div className="content has-text-right">
                            <h1 className="is-capitalized is-size-1 text-right is-family-monospace">
                                Zandraa
                                <br />
                                Perjési
                            </h1>
                            <p className="subtitle is-size-3 is-italic is-hidden-mobile">
                                software developer, in the making
                            </p>
                            <p>
                                <div className="head-to-social has-text-centered">
                                    <a href="https://www.linkedin.com/in/zandraa-perj%C3%A9si-516766162/">
                                        <span className="icon has-text-dark">
                                            <FaLinkedinIn size="fa-1x" />
                                        </span>
                                    </a>
                                </div>
                                <div className="head-to-social has-text-centered">
                                    <a href="https://github.com/zandraaperjesi">
                                        <span className="icon has-text-dark">
                                            <FaGithubAlt size="fa-2x" />
                                        </span>
                                    </a>
                                </div>
                            </p>
                        </div>
                    </div>
                </article>
            </div>
            <div className="scroll-down">
                <p>
                    <FaAngleDoubleDown />
                </p>
            </div>
        </div>
    </section>
);

export default Header;
